.page-catalog {
  background-color: #ffffff;
  height: 100%;
}

.page-catalog__title {
  padding: 0;
  margin: 27px 20px 44px;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
}

.page-catalog__body {
  margin: 0 auto;
  padding-top: 10px;
  width: 320px;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
}
